import { Component, Prop, State, h, getAssetPath } from '@stencil/core';

@Component({
  tag: 'chat-footer',
  styleUrl: 'chat-footer.scss',
  assetsDirs: ['assets'],
  shadow: true
})
export class ChatFooter {
  @Prop() hasIcons: boolean = true;
  @State() isListening: boolean = false;
  value: any; 

  render() {
    const buttonClass = this.value ? "dav-footer__icon" : 'dav-footer__icon__inactive';

    return (
      <section class='dav-footer noPadding'>
        <app-textfield class="dav-footer__textfield" placeholder='Mensaje'></app-textfield>
        <div class={buttonClass}>
          <img class='dav-footer__icon__img-send' src={getAssetPath('../../assets/icons/extra-icons/icon-send-message.svg')} />
        </div>
      </section>
    );
  }
}
